import React, {Component} from 'react';


class InputOrder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            // clientData: {
            //     domainUrl: '',
            //     industry: '',
            //     isNew: ''
            // },

        };
    }

    // submitDomainData = (event) => {
    //     event.preventDefault();

    //     this.setState({
    //         disabled: !this.state.disabled,
    //         rdySubmit: true,
    //     })
    // }

    // handleChangeClient = (event) => {

    //     let newDomainData;

    //     if (event.target.name === 'isNew') {

    //         newDomainData = {
    //             ...this.state.clientData,
    //             [event.target.name]: event.target.checked
    //         }

    //     } else {

    //         newDomainData = {
    //             ...this.state.clientData,
    //             [event.target.name]: event.target.value
    //         }

    //     }

    //     this.setState({
    //         clientData: newDomainData
    //     })

    // }


    render() {
        return (
            <React.Fragment>

                <h3 className="pb-5">General Client Data</h3>
                <form onSubmit={this.props.submitDomainData} className="w-75 mx-auto">
                    <div className="form-group row">
                        <label className="col-sm-3 col-form-label" htmlFor="domainUrl">Domain URL:</label>
                        <div className="col-sm-9">
                            <input
                                type="text"
                                className="form-control"
                                id="domainUrl"
                                onChange={this.props.handleChangeClient}
                                name="domainUrl"
                                value={this.props.domainUrl}
                                placeholder="e.g. https://xyz.com/"/>
                        </div>
                    </div>
                    <div className="form-group row">
                        <label className="col-sm-3 col-form-label" htmlFor="industry">Type of Industry:</label>
                        <div className="col-sm-9">
                            <input
                                type="text"
                                className="form-control"
                                id="industry"
                                onChange={this.props.handleChangeClient}
                                name="industry"
                                value={this.props.industry}
                                placeholder="e.g. weddings in poznan, wedding parties poznan, bridal parties poznan"/>
                        </div>
                    </div>

                    <div className="form-group row">
                        <div className="col-sm-3">Is client new?</div>
                        <div className="col-sm-2 d-flex justify-content-start">
                            <div className="form-check">
                                <input
                                    className="form-check-input"
                                    type="checkbox"
                                    id="isNew"
                                    onChange={this.props.handleChangeClient}
                                    name="isNew"
                                    value={this.props.isNew}/>
                                <label className="form-check-label" htmlFor="isNew">
                                    Check if new
                                </label>
                            </div>

                        </div>
                    </div>

                    <div className="form-group row">
                        <div className="col-sm-12">
                            {this.props.disabled === false && <button type="submit" className="btn btn-primary">Save Data</button>
}
                            {this.props.disabled !== false && <button disabled type="submit" className="btn btn-primary">Save Data</button>
}
{this.props.disabled ? <React.Fragment><div><span style={{fontSize : '20px',}} className="badge badge-info my-2 p-2">Go to next part!</span></div></React.Fragment> : null}
                        </div>
                    </div>
                </form>
               

            </React.Fragment>
        );
    }
}

export default InputOrder;